import axios from "axios";

export default {
  // Gets all players
  getPlayers: function() {
    return axios.get("/api/players");
  },
  // Deletes the player with the given id
  deletePlayer: function(id) {
    return axios.delete("/api/players/" + id);
  },
  // Saves a player to the database
  savePlayer: function(playerData) {
    return axios.post("/api/players", playerData);
  },

  getFantasyPros: function(players) {
    return axios.post("/api/scraper/", players);
  },

  msfSearch: function(player) {
    return axios.get("https://fd7bcee7-cd48-4fbb-875b-3be411:fender33@api.mysportsfeeds.com/v1.2/pull/nfl/2018-regular/active_players.json?player=" + player);
  },

  getAllArticles: function() {
    return axios.get("/api/scraper");
  }
}





